  updateProfileImage() {
    let t1,t2;
    this.translateService.get('SELECCIONE_IMAGEN').subscribe((value) => {
      t1=value;
    });
    this.translateService.get('TOME_IMAGEN').subscribe((value) => {
      t2=value;
    });
    let actionSheet = this.actionSheetCtrl.create({
      title: t1,
      buttons: [
        {
          text: t2,
          handler: () => {
            this.takePhotograph();
          }
        },
        {
          text: t1,
          handler: () => {
            this.selectImage();
          }
        }
      ]
    });
    actionSheet.present();
  }

  takePhotograph() : void
  {
   if (Camera['installed']()) {
    
     this._IMAGE
     
     .takePhotograph()
     .then((image)=>
     {
        this.fotot   	= image.toString();
        this.image   		= image.toString();
     })
     .catch((err)=>
     {
        console.log(err);
     });
   }
   else{
  
     this.fileInput.nativeElement.click();

   }
  }

  
  selectImage() : void
  {
   if (Camera['installed']()) {
    
     this._IMAGE
     .selectPhotograph()
     .then((image)=>
     {
        this.fotot   	= image.toString();
        this.image   		= image.toString();
     })
     .catch((err)=>
     {
        console.log(err);
     });
   }
   else{
  
     this.fileInput.nativeElement.click();
 
   }   
  }
  
  public processWebImage(event): void {
    if(event.target.files && event.target.files[0]){
          const reader: FileReader = new FileReader();
          reader.onload = (readerEvent: any) => {
          this.image = readerEvent.target.result;
          this._IMAGE.generateFromImage(this.image, 400, 400, 0.5, data => {
          this.image = data;
          this.fotot =  data;
      });
   };
      reader.readAsDataURL(event.target.files[0]);
    }
}


 

